.joystick {
    width: 200px;
    height: 200px;
    position: relative;
    box-shadow: 0 0 10px 2px gray;
    border-radius: 50%;
    background: white;
    margin-top: 20px;
    margin-left: 20px;

    &__stick {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        position: absolute;
        box-shadow: 0 0 25px 20px gray inset;
    }
}